
.Nav {
  background-color: rgb(45, 58, 44);
  font-family: 'Montserrat', sans-serif;
  color: white;
}

.ForHire {
  border: 1px solid rgb(83, 164, 81);
  padding: 0 5px 0 5px;
  border-radius: 4px;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.ForHire:hover {
  color:white;
  background-color: rgb(83, 164, 81);
}

.Hire {
  border: 1px solid rgb(71, 125, 243);
  padding: 0 5px 0 5px;
  border-radius: 4px;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.Hire:hover {
  color:white;
  background-color: rgb(71, 125, 243);
}

.NavItem {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
}

.ProfPic {
  -ms-flex-align: center;
      align-items: center;
  margin-left: 10px;
  margin-right: 5px;
}

@media (max-width: 575.98px) {
  .NavbarCustom {
    width: 100vw !important;
  }
}


